.slider__toggle {
  border-radius: 50%;
  background-color: #283645;
  opacity: 0.3;
  width: 6px;
  height: 6px;
  border: 3px solid #ffffff;
  padding: 0;
  margin: 0;
  box-sizing: content-box;

  &--current {
    border-radius: 50%;
    border: 3px solid #283645;
    width: 6px;
    height: 6px;
    background-color: #ffffff;
    margin: 0;
    padding: 0;
    opacity: 1;
    box-sizing: content-box;
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.1;
  }
}
