.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.promo {
  position: relative;
  background-image: url("../img/no-retina/mobile/index/back-mobile-no-retina.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -66px;
}
.promo::after {
  content: "";
  background-image: url("../img/no-retina/mobile/index/screen-nav-mobile-no-retina.jpg");
  background-repeat: no-repeat;
  background-size: 111px 197px;
  position: absolute;
  width: 111px;
  height: 197px;
  left: 104px;
  bottom: 56px;
}
.promo::before {
  content: "";
  background-image: url("../img/no-retina/mobile/index/phone-mobile-no-retina.png");
  background-repeat: no-repeat;
  background-size: 320px 286px;
  position: absolute;
  width: 320px;
  height: 286px;
  left: 0;
  bottom: 0;
  z-index: 1;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/retina/mobile/index/back-mobile-retina.jpg");
  }
  .promo::after {
    background-image: url("../img/retina/mobile/index/screen-nav-mobile-retina.jpg");
  }
  .promo::before {
    background-image: url("../img/retina/mobile/index/phone-mobile-retina.png");
  }
}
@media (min-width: 660px) {
  .promo {
    background-image: url("../img/no-retina/tablet/index/back-tablet-no-retina.jpg");
    background-size: cover;
    margin-top: -110px;
    min-height: 729px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }
  .promo::after {
    background-image: url("../img/no-retina/tablet/index/screen-nav-tablet-no-retina.jpg");
    background-size: 195px 346px;
    width: 195px;
    height: 346px;
    left: 371px;
    bottom: 146px;
  }
  .promo::before {
    background-image: url("../img/no-retina/tablet/index/phone-tablet-no-retina.png");
    background-size: 660px 545px;
    width: 660px;
    height: 545px;
  }
}
